.prof_Section{
    background: gray;
    grid-template-columns: 1fr 1fr;
    grid-template-rows: 1fr 1fr 1fr;
    display:grid;
    grid-template-areas:
    'sett sett'
    'ava desc'
    'prod prod';
}
.prof_Sett{
    grid-area: sett;
    border: black solid;
}
.prof_Ava{
    grid-area: ava;
    border: black solid;
}
.prof_Desc{
    grid-area: desc;
    border: black solid;
}
.prof_Prod{
    grid-area: prod;
    border: black solid;

}